import React from "react";
import { Row, Column } from "components/Grid.tsx";
import arrowIcon from "assets/img/icons/orange/arrow-right.svg";
import { withClickAnalytics } from "hocs/analytics";
import {
  ArrowLink,
  ArrowContainer,
  StyledSignUpReferenceLink,
  Container,
  Title,
  Body,
} from "./elements";

const ArrowLinkComponent = ({ text, analyticsPath }) => {
  const ApplyLink = withClickAnalytics(
    "Link",
    analyticsPath,
  )(StyledSignUpReferenceLink);

  return (
    <ArrowLink>
      <ArrowContainer>
        <img src={arrowIcon} alt="arrow icon" />
      </ArrowContainer>
      <ApplyLink analyticsPath={analyticsPath}>{text}</ApplyLink>
    </ArrowLink>
  );
};

const DetailRow = ({ imgSrc, title, body, link }) => (
  <Container imgSrc={imgSrc}>
    <Row padding={30} gutterWidth={26}>
      <Column span={3} spanMedium={2} spanSmall={0} />
      <Column span={6} spanMedium={8} spanSmall={12}>
        <Title>{title}</Title>
        <Body>{body}</Body>
        {link && ArrowLinkComponent(link)}
      </Column>
      <Column span={3} spanMedium={2} spanSmall={0} />
    </Row>
  </Container>
);

export default DetailRow;
